export default "@charset \"UTF-8\";\n.list .item.item-accordion {\n  line-height: 38px;\n  padding-top: 0;\n  padding-bottom: 0;\n  transition: 0.09s all linear;\n}\n.list .item.item-accordion.ng-hide {\n  line-height: 0px;\n}\n.list .item.item-accordion.ng-hide-add,\n.list .item.item-accordion.ng-hide-remove {\n  display: block !important;\n}\n.accordion {\n  width: 100%;\n  max-width: 360px;\n  margin: auto;\n  background: #fff;\n  border-radius: 4px;\n}\n.accordion .menu {\n  position: relative;\n  padding: 15px 15px 15px 45px;\n  color: #4d4d4d;\n  font-weight: bold;\n  border-bottom: 1px solid #ccc;\n  cursor: pointer;\n  transition: all 0.4s ease;\n}\n.accordion li:last-child .menu {\n  border-bottom: 0;\n}\n.accordion li i {\n  position: absolute;\n  top: 1.2rem;\n  left: 1rem;\n  color: #595959;\n  transition: all 0.4s ease;\n}\n.accordion li i.fa-chevron-down {\n  right: 1rem;\n  left: auto;\n}\n.accordion li.active i.fa-chevron-down {\n  transform: rotate(180deg);\n}\n.accordion li.active .menu {\n  color: #b63b4d;\n}\n.accordion li.active i {\n  color: #b63b4d;\n}\n/* Show submenu */\n.accordion li.active .submenu {\n  /*\n    height: 0 -> height: auto;는 transition이 동작하지 않는다.\n    max-height: 임의의 높이;를 지정하면 transition이 동작하지만 타이밍이 망가진다.\n    max-height: 1000px;과 max-height: 133px;을 비교해 보라!\n    height를 1000px으로 transition할 시간에 실제로는 133px정도만 transition하므로 여는 시간이 닫는 시간보다 빠르다.\n  */\n  /* max-height: 1000px; */\n  /* max-height: 133px; */\n}\n.submenu {\n  height: 0;\n  overflow: hidden;\n  background: #444359;\n  font-size: 14px;\n  transition: height 0.4s ease;\n}\n.submenu li {\n  border-bottom: 1px solid #4b4a5e;\n}\n.accordion li:last-child .submenu {\n  border-radius: 0 0 4px 4px;\n}\n.accordion li:last-child .submenu li:last-child {\n  border-bottom: 0;\n}\n.submenu a {\n  display: block;\n  text-decoration: none;\n  color: #d9d9d9;\n  padding: 12px;\n  padding-left: 42px;\n  transition: all 0.25s ease-in-out;\n}\n.submenu a:hover {\n  background: #b63b4d;\n  color: #fff;\n}\n.nav1,\n.nav2,\n.nav3 {\n  list-style: none;\n  padding: 10px;\n}\n.nav1 li,\n.nav2 li,\n.nav3 li {\n  padding: 10px 0 10px 10px;\n  border-top: solid 1px #ccc;\n}\n/*# sourceMappingURL=data:application/json;base64, */";